@charset "UTF-8";
@media print and (color) {
  * {
    -webkit-print-color-adjust: exact;
    print-color-adjust: exact; } }

html, body {
  /*changing width to 100% causes huge overflow and wrap*/
  height: 100%;
  font-family: Helvetica, verdana, Arial, sans-serif;
  background: #FFF;
  color: black;
  font-size: 8pt; }

@page {}.clearfix, .mobile_clearfix {
  *zoom: 1; }

.clearfix:before,
.clearfix:after, .mobile_clearfix:before, .mobile_clearfix:after {
  display: table;
  content: ""; }

.clearfix:after, .mobile_clearfix:after {
  clear: both; }

hr {
  color: #ccc;
  background-color: #ccc; }

h1, h2, h3, h4, h5, h6, code, pre, p {
  color: #000;
  text-transform: none;
  /* because sometimes I set all-caps */
  page-break-after: avoid; }

h1 {
  font-size: 11pt;
  margin-top: 1em;
  font-family: Helvetica, verdana, Arial, sans-serif; }

h2 {
  font-size: 10pt;
  font-family: Helvetica, verdana, Arial, sans-serif; }

h3 {
  font-size: 9pt;
  font-family: Helvetica, verdana, Arial, sans-serif; }

h4, h5, h6 {
  font-size: 8pt;
  font-family: Helvetica, verdana, Arial, sans-serif; }

p {
  padding-left: 0px !important; }

code, pre {
  font-size: 8pt;
  background-color: transparent;
  /* just in case the user configured browser to print backgrounds */ }

ul, ol {
  padding: 0px 0px rem-calc(10px) rem-calc(20px);
  margin: 0px 0px 15px; }

ul.no_bullets {
  padding-left: 0px; }
  ul.no_bullets li {
    list-style-type: none; }

ol li {
  list-style: lower-alpha; }

blockquote {
  background-image: none;
  /* though they may not print, Iï¿½d rather be sure */ }

a:link, a:visited {
  text-decoration: underline;
  color: #000; }

abbr:after, acronym:after {
  /* some HTML4 love */
  content: "" !important;
  /* I suspect I am one of the few who still use these elements */ }

.centre_text {
  text-align: center; }

button, input[type="submit"] {
  display: none; }

header[role=banner] {
  background: none;
  color: #000; }
  header[role=banner] img {
    filter: url(inverse.svg#negative);
    -webkit-filter: invert(100%);
    filter: invert(100%); }
  header[role=banner] .header_mobile_icons {
    display: none; }
  header[role=banner] .header_border {
    margin-bottom: 0px; }

#menu-primary-menu {
  display: none; }

article {
  /*
	$a:after > img {
		content: "";
	}
*/
  /*
	a:not(:local-link):after {
		content:" <" attr(href) "> ";
	}
*/ }
  article a {
    font-weight: bolder;
    text-decoration: none; }
  article a[href^=http]:after {
    content: "" !important; }
  article a[href^="#"]:after {
    content: "" !important; }
  article a[rel="external"]:after {
    content: "" !important; }
  article a[href]:after {
    content: "" !important; }
  article h1.work_title {
    margin: 0.5em 0px; }

ul, img {
  page-break-inside: avoid; }

#print-work .work_title {
  display: none; }

#print-work .print_work_img {
  display: none; }

#print-work #work_slider {
  display: none !important; }

#print-work #work_slider_navigation {
  display: none !important; }

#print-work #work_slider_description {
  display: none !important; }

#print-work button {
  display: none; }

#print-work .info_box {
  display: none; }

#print-work .work_pagination {
  display: none; }

ul.zebra_list {
  padding: 0px;
  width: 100%;
  margin: 0px; }
  ul.zebra_list img {
    max-width: 100%;
    height: auto;
    width: 100%;
    margin: 0px; }
  ul.zebra_list p {
    margin-left: 0px; }
  ul.zebra_list li.overlay_grid {
    page-break-inside: avoid;
    margin: 0px;
    padding: 0px;
    width: 100%;
    display: block;
    list-style: none;
    margin-bottom: rem-calc(40px);
    padding-bottom: rem-calc(40px);
    border-bottom: 1px solid #eee;
    clear: both; }
    ul.zebra_list li.overlay_grid:last-child {
      border-bottom: none; }
    ul.zebra_list li.overlay_grid .col-xs-3 {
      margin-left: 0px;
      width: 20% !important;
      float: left;
      display: inline-block; }
    ul.zebra_list li.overlay_grid .col-xs-9 {
      margin-left: 0px;
      width: 73% !important;
      float: left;
      display: inline-block; }
    ul.zebra_list li.overlay_grid .col-xs-12 {
      width: 100% !important; }
  ul.zebra_list ol, ul.zebra_list ul {
    padding-left: 18px; }
    ul.zebra_list ol li, ul.zebra_list ul li {
      list-style: outside none lower-latin;
      margin-bottom: rem-calc(5px);
      padding-bottom: 0px;
      border-bottom: 0px;
      text-align: left; }

footer[role="contentinfo"] .nav_menu-2 {
  display: none !important; }
